/* global Boss2018 */

import getScript from './getScript'

const url = '//vm.gtimg.cn/tencentvideo/script/modules/2018boss/small.js'

const initZmn = options => {
  if (process.env.NODE_ENV !== 'production') return

  getScript(url, () => {
    if (typeof Boss2018 === 'function') {
      let report = Boss2018(options.appname)

      if (!options.zepto) return
      if (typeof report.cgi !== 'function') return

      let $ = options.zepto
      let $doc = $(document)
      let defaultStartTime = Date.now() // 当 ajaxBeforeSend 注册晚于请求时用默认值

      $doc.on('ajaxBeforeSend', (e, xhr, options) => {
        options.startTime = Date.now()
      })

      $doc.on('ajaxSuccess', (e, xhr, options, data) => {
        let startTime = options.startTime || defaultStartTime

        report.cgi({
          cgiurl: options.url, // cgi
          delay: Date.now() - startTime, // 延时
          bizcode: 0, // 接入返回码
          bizcode_server: data.code, // 服务返回码
          starttime: startTime, // 请求开始时间
          bismsg: data.msg, // 返回信息
          httpcode: xhr.status
        })
      })

      $doc.on('ajaxError', (e, xhr, options, error) => {
        let startTime = options.startTime || defaultStartTime

        report.cgi({
          cgiurl: options.url, // cgi
          delay: Date.now() - startTime, // 延时
          starttime: startTime, // 请求开始时间
          httpcode: xhr.status
        })
      })
    }
  })
}

export default initZmn
