/* global Omplayer */

import Raven from 'raven-js'
import { dsn } from './lib/sentry'

if (process.env.NODE_ENV !== 'production') {
  console.log(`当前运行环境: ${process.env.NODE_ENV}`)
} else {
  // sentry 监控
  Raven.config(dsn, {
    ignoreUrls: [/brace\.video\.qq\.com/, /vm\.gtimg\.cn/],
    release: process.env.RAVEN_VERSION
  }).install()
}

// 载入样式
require('./assets/iframe.less')

// 啄木鸟上报
import initZmn from './lib/zmn'
initZmn({ zepto: window.OMZepto, appname: 'omplayer_iframe' })

// 加载相关组件
import querystring from 'querystring'

// 开发环境加载播放器
if (process.env.NODE_ENV !== 'production') {
  require('./index.js')
}

// 解析 querystring
const query = querystring.parse(location.search.slice(1))

const ALLOWOPTIONS = ['src', 'omvid', 'platform', 'sdtfrom', 'level', 'iv', 'preload', 'autoplay', 'loop', 'muted',
  'poster', 'playbackRate', 'controlsDuration', 'controlsMuted', 'controlsLevel', 'showBottomProgressbar', 'fit']

Object.keys(query).forEach(key => {
  if (!ALLOWOPTIONS.includes(key)) {
    delete query[key]
    return
  }
  if (query[key] === 'true') query[key] = true
  if (query[key] === 'false') query[key] = false
})

// 初始化视频播放器
let video = new Omplayer({
  wrapElement: '#container',
  width: '100%',
  height: '100%',
  platform: '207010',
  sdtfrom: 'v7160', ...query
})

// 忽略一些错误减少触发 sentry 告警
video.on(`error`, (e, data) => {
  if (e.type === 'error' && data && data.isIgnoreError) {
    e.stopPropagation()
  }
})

window.video = video
